.tippy-box[data-animation=fade][data-state=hidden] {
  opacity: 0; }

[data-tippy-root] {
  max-width: calc(100vw - 10px); }

.tippy-box {
  position: relative;
  background-color: #294252;
  color: #fff;
  border-radius: 4px;
  white-space: normal;
  outline: 0;
  transition-property: transform,visibility,opacity; }

.tippy-box[data-placement^=top] >
.tippy-arrow {
  bottom: 0; }

.tippy-box[data-placement^=top] >
.tippy-arrow:before {
  bottom: -7px;
  left: 0;
  border-width: 8px 8px 0;
  border-top-color: initial;
  transform-origin: center top; }

.tippy-box[data-placement^=bottom] >
.tippy-arrow {
  top: 0; }

.tippy-box[data-placement^=bottom] >
.tippy-arrow:before {
  top: -7px;
  left: 0;
  border-width: 0 8px 8px;
  border-bottom-color: initial;
  transform-origin: center bottom; }

.tippy-box[data-placement^=left] >
.tippy-arrow {
  right: 0; }

.tippy-box[data-placement^=left] >
.tippy-arrow:before {
  border-width: 8px 0 8px 8px;
  border-left-color: initial;
  right: -7px;
  transform-origin: center left; }

.tippy-box[data-placement^=right] >
.tippy-arrow {
  left: 0; }

.tippy-box[data-placement^=right] >
.tippy-arrow:before {
  left: -7px;
  border-width: 8px 8px 8px 0;
  border-right-color: initial;
  transform-origin: center right; }

.tippy-box[data-inertia][data-state=visible] {
  transition-timing-function: cubic-bezier(0.54, 1.5, 0.38, 1.11); }

.tippy-arrow {
  width: 16px;
  height: 16px;
  color: #333; }

.tippy-arrow:before {
  content: "";
  position: absolute;
  border-color: transparent;
  border-style: solid; }

.tippy-content {
  position: relative;
  padding: 5px 9px;
  z-index: 1; }

.tippy-box[data-theme~=light] {
  color: #3f3f3f;
  box-shadow: 6px 4px 7px 0px rgba(0, 0, 0, 0.3);
  background-color: #fff; }
  .tippy-box[data-theme~=light] p:last-child {
    margin-bottom: 0; }

.tippy-box[data-theme~=light][data-placement^=top] >
.tippy-arrow:before {
  border-top-color: #fff; }

.tippy-box[data-theme~=light][data-placement^=bottom] >
.tippy-arrow:before {
  border-bottom-color: #fff; }

.tippy-box[data-theme~=light][data-placement^=left] >
.tippy-arrow:before {
  border-left-color: #fff; }

.tippy-box[data-theme~=light][data-placement^=right] >
.tippy-arrow:before {
  border-right-color: #fff; }

.tippy-box[data-theme~=light] >
.tippy-backdrop {
  background-color: #fff; }

.tippy-box[data-theme~=light] >
.tippy-svg-arrow {
  fill: #fff; }
